import axios from 'axios'; // import { isMobile } from 'mobile-device-detect'

export default {
  data: function data() {
    return {
      isCallBack: true,
      trangThaiHoSoList: [],
      loading: true,
      currentStep: '0',
      counterData: [],
      detailState: 0,
      dialogVerifycation: false,
      verificationApplicantCreateDossier: false
    };
  },
  beforeDestroy: function beforeDestroy() {
    if (typeof window !== 'undefined') {
      window.removeEventListener('resize', this.onResize, {
        passive: true
      });
    }
  },
  mounted: function mounted() {
    this.onResize();
    window.addEventListener('resize', this.onResize, {
      passive: true
    });

    if (this.isMobile) {
      $('section#content').css('padding-left', '0px');
    }
  },
  computed: {
    currentIndex: function currentIndex() {
      return this.$store.getters.index;
    },
    loadingGov: function loadingGov() {
      return this.$store.getters.loadingGov;
    },
    activeGetCounter: function activeGetCounter() {
      return this.$store.getters.activeGetCounter;
    },
    // viewMobile () {
    //   return isMobile
    // },
    pathLanding: function pathLanding() {
      return '/danh-sach-ho-so';
    },
    currentUser: function currentUser() {
      return this.$store.getters.loadingInitData.user;
    },
    userLoginInfomation: function userLoginInfomation() {
      return this.$store.getters.getUserLogin;
    },
    isMobile: function isMobile() {
      return this.$store.getters.getIsMobile;
    }
  },
  created: function created() {
    var vm = this;
    var isMobile = window.innerWidth < 1264;
    vm.$store.commit('setIsMobile', isMobile); //

    try {
      vm.verificationApplicantCreateDossier = hasVerificationCreateDossier;
    } catch (error) {} // 


    axios.get('/o/v1/opencps/users/' + window.themeDisplay.getUserId()).then(function (response) {
      var userData = response.data;
      vm.$store.commit('setUserLogin', userData);
    }).catch(function (error) {}); // if (window.location.href.includes('/m/') && isMobile) {
    //   $('head meta[name=viewport]').remove()
    // } else {
    //   if ($('head meta[name=viewport]').length === 0) {
    //     $('head').append('<meta name="viewport" content="width=device-width, initial-scale=1.0"/>')
    //   }
    // }

    vm.$nextTick(function () {
      vm.loading = true;
      vm.$store.dispatch('loadMenuConfigToDo').then(function (result) {
        vm.loading = false;

        if (result.length !== 0) {
          vm.trangThaiHoSoList = result;
          var route = vm.$router.history.current;
          var currentParams = vm.$router.history.current.params;

          if (!currentParams.hasOwnProperty('index') && !currentParams.hasOwnProperty('serviceCode') && route.name !== 'ThanhToanThanhCong') {
            vm.trangThaiHoSoList[0]['active'] = true;
            vm.$router.push({
              path: vm.pathLanding + '/0',
              query: {
                q: vm.trangThaiHoSoList[0]['queryParams']
              }
            });
          } else if (currentParams.hasOwnProperty('index')) {
            vm.trangThaiHoSoList[currentParams.index]['active'] = true;
          }

          vm.loadingCounter();
        }
      }); // 

      setInterval(function () {
        vm.loadingCounter();
      }, 3 * 60 * 1000);
    });
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {
      var currentParams = vm.$router.history.current.params;

      if (currentParams.hasOwnProperty('index') && vm.isCallBack) {
        vm.isCallBack = false;
        vm.$store.commit('setIndex', currentParams.index);

        if (currentParams.hasOwnProperty('id')) {
          vm.detailState = 1;
        } else {
          vm.detailState = 0;
        }
      }
    });
  },
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      var isMobile = window.innerWidth < 1264;
      vm.$store.commit('setIsMobile', isMobile); // if (window.location.href.includes('/m/') && vm.viewMobile) {
      //   $('head meta[name=viewport]').remove()
      // } else {
      //   if ($('head meta[name=viewport]').length === 0) {
      //     $('head').append('<meta name="viewport" content="width=device-width, initial-scale=1.0"/>')
      //   }
      // }

      var currentParams = newRoute.params;
      var currentQuery = newRoute.query;

      if (currentQuery.hasOwnProperty('step')) {
        vm.currentStep = String(currentQuery.step);
      } else {
        vm.currentStep = '0';
      }

      if (currentQuery.hasOwnProperty('recount')) {
        vm.loadingCounter();
      }

      if (currentParams.hasOwnProperty('id')) {
        vm.detailState = 1;
      } else {
        vm.detailState = 0;
      }
    },
    activeGetCounter: function activeGetCounter(val) {
      var vm = this;
      setTimeout(function () {
        vm.loadingCounter();
      }, 300);
    }
  },
  methods: {
    toTableIndexing: function toTableIndexing(item, index) {
      var vm = this;

      try {
        if (vm.isMobile && $('#table-dossier')) {
          $('html, body').animate({
            scrollTop: $('#table-dossier').offset().top
          }, 200, 'linear');
        }
      } catch (error) {}

      this.$store.commit('setIndex', index);
      vm.$router.push({
        path: vm.pathLanding + '/' + index,
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1,
          q: item.queryParams
        }
      });
    },
    filterSteps: function filterSteps(item, index) {
      var vm = this;

      try {
        if (vm.isMobile && $('#table-dossier')) {
          $('html, body').animate({
            scrollTop: $('#table-dossier').offset().top
          }, 200, 'linear');
        }
      } catch (error) {}

      var currentQuery = this.$router.history.current.query;
      var currentParams = this.$router.history.current.params;
      var queryString = this.trangThaiHoSoList[index].queryParams;
      /* test Local */

      /* let queryString = this.trangThaiHoSoList[0].queryParams */

      if (currentQuery !== null && currentQuery !== undefined) {
        var coma = queryString.lastIndexOf('=');

        if (coma > 0) {
          queryString = queryString.substr(0, coma + 1);
        }
      }

      this.currentStep = String(item.stepCode);
      vm.$router.push({
        path: vm.pathLanding + '/' + currentParams.index,
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1,
          q: queryString + item.stepCode,
          step: item.stepCode
        }
      });
    },
    loadingCounter: function loadingCounter() {
      var vm = this;
      var filter = {
        originality: vm.getOriginality()
      };
      vm.$store.dispatch('loadingCounterHoSo', filter).then(function (result) {
        vm.counterData = result.data;

        var _loop = function _loop(key) {
          vm.trangThaiHoSoList[key]['counter'] = 0;

          if (vm.trangThaiHoSoList[key].hasOwnProperty('items')) {
            var parentCount = 0;

            for (var keyChild in vm.trangThaiHoSoList[key].items) {
              vm.trangThaiHoSoList[key].items[keyChild]['counter'] = 0;

              for (var countKey in vm.counterData) {
                if (String(vm.counterData[countKey].stepCode) === String(vm.trangThaiHoSoList[key].items[keyChild].stepCode)) {
                  var countCurrent = vm.counterData[countKey].totalCount;
                  vm.trangThaiHoSoList[key].items[keyChild]['counter'] = countCurrent;
                  parentCount = parentCount + countCurrent;
                  break;
                }
              }
            }

            vm.trangThaiHoSoList[key]['counter'] = parentCount;
          } else {
            if (vm.trangThaiHoSoList[key].queryParams.indexOf('step') >= 0) {
              if (vm.trangThaiHoSoList[key]['menuType'] === 2) {
                var _filter = {
                  queryParams: vm.trangThaiHoSoList[key].queryParams
                };
                vm.$store.dispatch('loadingCounterNotStep', _filter).then(function (result) {
                  vm.trangThaiHoSoList[key]['counter'] = result.total;
                }).catch(function () {
                  vm.trangThaiHoSoList[key]['counter'] = 0;
                });
              } else {
                var stepParent = vm.trangThaiHoSoList[key].queryParams.split('step=');
                var countParent = 0;

                for (var _countKey in vm.counterData) {
                  if (String(vm.counterData[_countKey].stepCode) === String(stepParent[1])) {
                    var _countParent = vm.counterData[_countKey].totalCount;
                    break;
                  }
                }

                vm.trangThaiHoSoList[key]['counter'] = countParent;
              }
            } else {
              var _filter2 = {
                queryParams: vm.trangThaiHoSoList[key].queryParams
              };
              vm.$store.dispatch('loadingCounterNotStep', _filter2).then(function (result) {
                vm.trangThaiHoSoList[key]['counter'] = result.total;
              }).catch(function () {
                vm.trangThaiHoSoList[key]['counter'] = 0;
              });
            }
          }
        };

        for (var key in vm.trangThaiHoSoList) {
          _loop(key);
        }

        vm.loading = false;
      });
    },
    doAddDVC: function doAddDVC() {
      var vm = this;

      if (vm.verificationApplicantCreateDossier && vm.userLoginInfomation && vm.userLoginInfomation['verification'] && String(vm.userLoginInfomation['verification']) === '2') {
        vm.dialogVerifycation = true;
      } else {
        vm.$router.push('/add-dvc/0'); // vm.$router.push('/linh-vuc-thu-tuc')
      }
    },
    onResize: function onResize() {
      var vm = this;
      var isMobile = window.innerWidth < 1264;
      vm.$store.commit('setIsMobile', isMobile);
    }
  }
};