import Vue from 'vue';
import toastr from 'toastr';
import axios from 'axios';
import $ from 'jquery';
import TinyPagination from '../../components/pagging/opencps_pagination';
Vue.use(toastr);
export default {
  props: ['index'],
  components: {
    'tiny-pagination': TinyPagination
  },
  data: function data() {
    return {
      applicantInfos: '',
      nameTitle: '',
      creditTitle: '',
      documentApplicantList: '',
      totalDocument: 0,
      documentPage: 1,
      numberPerPage: 15,
      statusList: [{
        text: 'Chưa duyệt',
        value: 0
      }, {
        text: 'Có hiệu lực',
        value: 1
      }, {
        text: 'Hết hiệu lực',
        value: 2
      }],
      status: '',
      statusCreate: '',
      fileTemplateNoCreate: '',
      fileTemplateList: [],
      fileTemplateNo: '',
      fileName: '',
      fileNo: '',
      fileUpdate: '',
      fileNameUpdate: '',
      fileNoSearch: '',
      keySearch: '',
      dialog_createDocument: false,
      updateFile: false,
      loadingAction: false,
      loadingTable: false,
      dialogPDF: false,
      valid: false,
      documentListHeader: [{
        text: 'STT',
        align: 'center',
        sortable: false
      }, {
        text: 'Tên tài liệu',
        align: 'center',
        sortable: false
      }, {
        text: 'Mã số',
        align: 'center',
        sortable: false
      }, {
        text: 'Hồ sơ gốc',
        align: 'center',
        sortable: false
      }, {
        text: 'Tình trạng',
        align: 'center',
        sortable: false
      }, {
        text: 'Thao tác',
        align: 'center',
        sortable: false
      }],
      maxFileSize: 100,
      typeCreate: 'create',
      documentSelect: '',
      fileNameView: '',
      srcDownload: '',
      applicantIdNo: ''
    };
  },
  computed: {
    originality: function originality() {
      var vm = this;
      return vm.getOriginality();
    }
  },
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      var currentParams = newRoute.params;
      var currentQuery = newRoute.query;
    },
    index: function index(val) {
      this.applicantIdNo = val;
    }
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var current = vm.$router.history.current;
      var query = vm.$router.history.current.query;
      vm.applicantIdNo = vm.index;
    });
  },
  mounted: function mounted() {
    var vm = this;
    vm.applicantIdNo = vm.index;
  },
  methods: {
    initData: function initData() {
      var vm = this;
      vm.getApplicantDocument();
      vm.getFileItems();
    },
    getApplicantInfos: function getApplicantInfos() {
      var vm = this;
      var filter = {
        applicantId: vm.index
      };
      vm.$store.dispatch('getApplicantInfos', filter).then(function (result) {
        vm.applicantInfos = result;
        vm.getApplicantType(vm.applicantInfos);
      }).catch(function () {});
    },
    getApplicantDocument: function getApplicantDocument() {
      var vm = this;
      var filter = {
        start: vm.documentPage * vm.numberPerPage - vm.numberPerPage,
        end: vm.documentPage * vm.numberPerPage,
        applicantIdNo: vm.applicantIdNo,
        fileTemplateNo: vm.fileTemplateNo,
        status: vm.originality === 1 ? 1 : vm.status,
        keywordSearch: vm.keySearch,
        fileNoSearch: vm.fileNoSearch,
        applicantDataType: ''
      };
      vm.loadingTable = true;
      vm.$store.dispatch('getApplicantDocument', filter).then(function (result) {
        if (result.hasOwnProperty('data')) {
          vm.documentApplicantList = result.data;
        } else {
          vm.documentApplicantList = [];
        }

        vm.totalDocument = result['total'];
        vm.loadingTable = false;
      }).catch(function () {
        vm.loadingTable = false;
        vm.documentApplicantList = [];
        vm.totalDocument = 0;
      });
    },
    getFileItems: function getFileItems() {
      var vm = this;
      var filter = {
        status: 1
      };
      vm.$store.dispatch('getFileItems', filter).then(function (result) {
        if (result.hasOwnProperty('data')) {
          vm.fileTemplateList = result.data;
        } else {
          vm.fileTemplateList = [];
        }
      }).catch(function () {});
    },
    showCreatedocument: function showCreatedocument() {
      var vm = this;
      vm.typeCreate = 'create';
      vm.dialog_createDocument = true;
      vm.$refs.form.reset();
      vm.$refs.form.resetValidation();
      vm.fileNameView = '';
    },
    getDocumentTypeIcon: function getDocumentTypeIcon(type) {
      var vm = this;
      var typeDoc = 'doc,docx';
      var typeExcel = 'xls,xlsx';
      var typeImage = 'png,jpg,jpeg';

      if (type) {
        if (typeDoc.indexOf(type.toLowerCase()) >= 0) {
          return {
            icon: 'fas fa fa-file-word-o',
            color: 'blue',
            size: 14
          };
        } else if (typeExcel.indexOf(type.toLowerCase()) >= 0) {
          return {
            icon: 'fas fa fa-file-excel-o',
            color: 'green',
            size: 14
          };
        } else if (type.toLowerCase() === 'pdf') {
          return {
            icon: 'fa fa-file-pdf-o',
            color: 'red',
            size: 14
          };
        } else if (typeImage.indexOf(type.toLowerCase()) >= 0) {
          return {
            icon: 'fas fa fa-file-image-o',
            color: 'primary',
            size: 14
          };
        } else {
          return {
            icon: 'fas fa fa-paperclip',
            color: '',
            size: 14
          };
        }
      } else {
        return '';
      }
    },
    changeFilterSearch: function changeFilterSearch() {
      var vm = this;
      vm.documentPage = 1;
      setTimeout(function () {
        vm.getApplicantDocument();
      }, 200);
    },
    changePage: function changePage(config) {
      var vm = this;
      vm.documentPage = config.page;
      vm.getApplicantDocument();
    },
    showEditDocument: function showEditDocument(item) {
      var vm = this;
      vm.documentSelect = item;
      vm.typeCreate = 'update';
      vm.fileNameView = item.filePath;
      vm.fileTemplateNoCreate = vm.fileTemplateList.filter(function (items) {
        return items.fileTemplateNo === item.fileTemplateNo;
      })[0];
      vm.statusCreate = item.status;
      vm.fileName = item.fileName;
      vm.fileNo = item.fileNo;
      vm.dialog_createDocument = true;
    },
    validFileUpload: function validFileUpload(file) {
      var vm = this;
      var passed = true;
      var getFileType = file.name ? file.name.split('.') : '';
      var fileType = getFileType ? getFileType[getFileType.length - 1] : '';
      var fileTypeAllow = vm.fileTemplateNoCreate && vm.fileTemplateNoCreate.fileType ? vm.fileTemplateNoCreate.fileType.toUpperCase().split(',') : '';
      var fileSizeAllow = vm.fileTemplateNoCreate && vm.fileTemplateNoCreate.size ? vm.fileTemplateNoCreate.size : vm.maxFileSize;
      var fileTypeInput = fileTypeAllow ? fileTypeAllow.filter(function (item) {
        return item === fileType.toUpperCase();
      }) : '';

      if (fileTypeInput && fileTypeInput.length > 0 || !fileTypeAllow) {
        if (Number(file.size) <= fileSizeAllow * 1048576 || !fileSizeAllow) {
          passed = true;
        } else {
          passed = false;
          toastr.clear();
          toastr.error('Tài liệu tải lên dung lượng tối đa là ' + fileSizeAllow + 'MB');
        }
      } else {
        passed = false;
        toastr.clear();
        toastr.error('Tài liệu tải lên chỉ chấp nhận các định dạng ' + vm.fileTemplateNoCreate.fileType);
      }

      return passed;
    },
    attachDocument: function attachDocument() {
      var vm = this;
    },
    downloadDocument: function downloadDocument(item) {
      var vm = this;
      vm.srcDownload = '';
      var url = item.filePath;
      var filter = {
        applicantDataId: item.applicantDataId
      };
      vm.$store.dispatch('getFileAttach', filter).then(function (result) {
        vm.srcDownload = result;
        setTimeout(function () {
          document.getElementById('downloadFile').click();
        }, 100);
      }).catch(function () {});
    },
    viewDocument: function viewDocument(item) {
      var vm = this;
      vm.srcDownload = '';
      var url = item.filePath;
      var filter = {
        applicantDataId: item.applicantDataId
      };
      vm.$store.dispatch('getFileAttach', filter).then(function (result) {
        var fileType = item.fileExtension.toLowerCase();

        if (fileType === 'png' || fileType === 'jpg' || fileType === 'jpeg' || fileType === 'pdf' || fileType === 'gif' || fileType === 'tif' || fileType === 'tiff') {
          vm.dialogPDF = true;
          document.getElementById('dialogPDFPreview').src = result;
        } else {
          vm.srcDownload = result;
          setTimeout(function () {
            document.getElementById('downloadFile').click();
          }, 100);
        }
      }).catch(function () {});
    },
    getApplicantType: function getApplicantType(item) {
      var vm = this;

      if (item.applicantIdType === '') {
        vm.nameTitle = 'Tên công dân, tổ chức, doanh nghiệp';
        vm.creditTitle = 'Số CMND/ căn cước, mã số thuế doanh nghiệp';
      } else if (item.applicantIdType === 'citizen') {
        vm.nameTitle = 'Tên công dân';
        vm.creditTitle = 'Số CMND/ căn cước';
      } else if (item.applicantIdType === 'business') {
        vm.nameTitle = 'Tên doanh nghiệp';
        vm.creditTitle = 'Mã số thuế doanh nghiệp';
      } else {
        vm.nameTitle = 'Tên cơ quan, tổ chức';
        vm.creditTitle = 'Mã cơ quan, tổ chức';
      }
    },
    getStatus: function getStatus(val) {
      if (String(val) === '1') {
        return 'Có hiệu lực';
      } else if (String(val) === '2') {
        return 'Hết hiệu lực';
      } else {
        return 'Chưa duyệt';
      }
    },
    goBack: function goBack() {
      window.history.back();
    }
  }
};